.reviews-section {
    margin-top: 3.5rem;
    margin-bottom: 1rem;
}

nav ul {
    margin: 0;
}

nav ul {
    padding: 0;
    font-size: 1rem;
    height: 2.5rem;
    list-style: none;
    position: fixed;
    width: 100%;
    background-color: #F2DF3A;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

nav div select {
    height: 2rem;
    text-align: center;
    border-radius: 5px;
}

nav div button {
    height: 2rem;
    width: 2rem;
    background-color: #3AB4F2;
    color: #F2DF3A;
    cursor: pointer;
    font-size: 1rem;
    text-shadow: 1px 1px 1px black;
    border-radius: 5px;
}

li a {
    text-decoration: none;
    color: #3AB4F2
}

li {
    padding-right: 0.5rem;
    color: #3AB4F2
}

li:hover {
    cursor: pointer;
    text-shadow: 1px 1px 2px #3ab5f2d0;
}

li a:hover {
    text-decoration: none;
}

@media screen and (max-width: 1100px) {
    .reviews-section {
        margin-top: 4rem;
    }
    nav ul {
        padding-top: 0.25rem;
        font-size: 1.5rem;
        height: 2.5rem;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-end;
        text-align: right;
    }
    nav ul li {
        background-color: #F2DF3A;
        width: 15rem;
    }
    nav div {
        position: absolute;
        left: 1rem;
    }
}

#preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
#loader {
    display: block;
    position: relative;
    left: 50%;
    top: 50%;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #9370DB;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
}
#loader:before {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    right: 5px;
    bottom: 5px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #BA55D3;
    -webkit-animation: spin 3s linear infinite;
    animation: spin 3s linear infinite;
}
#loader:after {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #FF00FF;
    -webkit-animation: spin 1.5s linear infinite;
    animation: spin 1.5s linear infinite;
}
@-webkit-keyframes spin {
    0%   {
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes spin {
    0%   {
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}